<template>
  <section class="swaps">
    <header class="swaps__header">
      <Heading>Swaps</Heading>
    </header>
    <main class="swaps__container">
      <SwapsGrid />
    </main>
  </section>
</template>

<script>
import SwapsGrid from '@/views/admin/Swaps/SwapsGrid'
import { useStore } from 'vuex'
import { onBeforeUnmount } from 'vue'
import swapsModule from '@/store/modules/swaps'
import Heading from '@/components/Admin/Heading'

export default {
  name: 'Swaps',
  components: {
    Heading,
    SwapsGrid
  },
  setup () {
    const store = useStore()
    if (!store.state.swaps) {
      store.registerModule('swaps', swapsModule)
    }
    onBeforeUnmount(() => {
      if (store.state.swaps) {
        store.unregisterModule('swaps')
      }
    })
  }
}
</script>

<style lang="scss">
.swaps {
  padding-left: 20px;
  padding-top: 36px;

  &__header {
    padding-left: 20px;
    margin-bottom: 36px;
  }
